import React from 'react';
import {Switch, Route,BrowserRouter} from 'react-router-dom';
import { withRouter } from "react-router-dom";
import {addListActionCreator} from './models/actioncreators/addlistactioncreator';
import {editListActionCreator} from './models/actioncreators/editListActionCreator';
import {delListActionCreator} from './models/actioncreators/dellistactioncreator';
import {store} from './models/store';
import {AddList} from './components/AddList';
import ShowLists from './components/ShowLists';
import {actionNames} from './models/actiontype'
import UpdateList from './components/UpdateList';
import {Start}  from './components/Start';
import './App.css';
export class App extends React.Component{
  constructor(props){
    super(props);
    this.obj={};
    this.delDB=[];
    this.decider="";
    this.oldObj={};
    this.uextype="";
    this.uamount="";
    this.ucheck="";
    this.udesc="";
    this.id=1;
    this.state={uextype:this.uextype,uamount:this.uamount,udesc:this.udesc,check:this.check};
   this.datab=[]; 
  }
  addList(event){
    if(event.target.id==="check"){
      this.obj[event.target.id]=event.target.checked;
    }
    else{
    this.obj[event.target.id]=event.target.value;
    }
  }
  saveList(){
    var action = addListActionCreator(this.obj,this.id,this.datab,actionNames.ADD);
    store.dispatch(action);
    this.id++;
    this.obj={};
    alert('List Added. Click On Show Lists To View Added Lists')
  }
  editList(listObj){
    this.decider="update";
    this.oldObj=listObj;
    this.uextype=listObj.extype;
    this.uamount=listObj.amount;
    this.udesc=listObj.desc;
    this.check=listObj.check;
    store.dispatch(editListActionCreator(listObj,actionNames.UPDATE));
    this.setState({...this.state});
  }
  updateObj(event){
    if(event.target.id==="check"){
      this.ucheck=event.target.checked;
      this.setState({...this.state,ucheck:this.ucheck});
    }
    else if(event.target.id==="amount"){
      this.uamount=event.target.value;
      
      this.setState({...this.state,uamount:this.uamount});
    }
    else if(event.target.id==="extype"){
      
      this.uextype=event.target.value;
      this.setState({...this.state,uextype:this.uextype});
    }
    else if(event.target.id==="desc"){
      
      this.udesc=event.target.value;
      this.setState({...this.state,udesc:this.udesc});
    }
  }
  
  showUpdated(currObj){
    var temp;
  store.getState().addition.db.forEach((indexEle)=>{
    if(indexEle.id===this.oldObj.id){
      for(let key in indexEle){
        if(key!=="id" && key!=="cname" && key!=="markForDelete"){
          indexEle[key]=currObj[key];
        if(key==="check"){
          temp=currObj[key];
        }
        }
      }
      for(let key in indexEle){
        if(key==="cname"){
          if(temp===true){
            indexEle[key]='list-group-item-success';
          }
          else{
            indexEle[key]='list-group-item-danger';
          }
        }
      }
    }
  })
  this.decider="";
  this.setState({...this.state});
  }

  deleteList(obj){
    store.dispatch(delListActionCreator(obj,this.delDB,actionNames.DELETE));
    var arr=[];
    arr=store.getState().addition.db.filter(ele=>!ele.markForDelete);
     
    store.dispatch(addListActionCreator({},"",arr,'updateArray'));
    this.datab=arr;
  }



 load(){
    if(localStorage){
        if(localStorage['database']){
            var database = JSON.parse(localStorage['database']);
            store.dispatch(addListActionCreator({},"",database,"local"));
            this.datab=database;
          }
        else{
            alert("No Data Exist to Load");
        }
    }
    else{
        alert("Ur Browser is Outdated");
    }
}
save(){
    if(localStorage){
       localStorage['database']=JSON.stringify(this.datab);
       alert("Record Saved");
    }
    else{
        alert("Ur Browser is Outdated");
    }
}
  render(){
    if(this.decider==="update"){
      return(
        <div className="container"> 
        <h1 className="center">Expense List</h1>
        <br/> 
        <br/> 
      <UpdateList uextype={this.uextype} udesc={this.udesc} uamount={this.uamount} ucheck={this.ucheck} updateObj={this.updateObj.bind(this)} showUpdated={this.showUpdated.bind(this)}/>
      </div>
      )
    }
    else {
      return(
        //<BrowserRouter basename="/">
    <div className="container"> 
            <h1 className="center">Expense List</h1>
            <br/> 
            <br/> 
      <Start/>
      <Switch>
      <Route   exact path="/addlist" render={props => 
  (<AddList addList={this.addList.bind(this)} saveList={this.saveList.bind(this)}/>)
}/>
      <Route   path="/showlists" render={props => 
  (<ShowLists save={this.save.bind(this)} load={this.load.bind(this)} deleteList={this.deleteList.bind(this)} editList={this.editList.bind(this)}/>)
}/> 
      </Switch>  
      <br/> 
      <br/>
  </div>
  //</BrowserRouter>
      )
    }
  }
}

export default withRouter(App);