import { actionNames } from "../actiontype"
import { opr } from '../../utils/config'
export const editListReducer = (initState={list:{},operation:""}, action)=>{
    if(action.type===actionNames.UPDATE){
        var editlist=action.payload.list;
        opr.operationName=action.type;
        return {...initState,list:editlist,operation:opr};
    }
    return initState; 
}     