import React from 'react';
export const AddList=(props)=>{
    return(
        <>
        <form className="was-validated">
        <div className="form-group">
      <label htmlFor="extype">Expense Type</label>
      <input type="text" onChange={props.addList} className="form-control is-valid" id="extype" placeholder="Enter Expense Type" required/>
  </div>
        <div className="form-group">
    <label htmlFor="desc">Description</label>
    <textarea onChange={props.addList} className="form-control is-invalid" id="desc" placeholder="Enter Description" required></textarea>
  </div>
  <div className="form-group">
        <label htmlFor="amount">Amount</label>
      <input type="text" onChange={props.addList} className="form-control is-valid" id="amount" placeholder="Enter Amount Here" required/>
      </div>
  </form>
  <div className="custom-control custom-checkbox">
  <input onChange={props.addList} type="checkbox" className="custom-control-input" id="check"/>
  <label className="custom-control-label" htmlFor="check">Paid</label>
</div>
<br/>
<button type="button" onClick={()=>{props.saveList()}} className="btn btn-outline-primary ml-6">Add</button>
        </> 
    )
}  
