import React from 'react';
import '../App.css'
export const ShowList= (props)=>{
    return ( 
       <>
       <ul id="showlist" className="list-group" className={props.list.cname}>
  
  <li className="list-group-item" className={props.list.cname} >Expense Type:-{props.list.extype}</li>
  <li className="list-group-item" className={props.list.cname}>Description:-{props.list.desc}</li>
  <li className="list-group-item" className={props.list.cname}>Amount:-{props.list.amount}</li>
  <li className="list-group-item" className={props.list.cname} >
  <div className="custom-control custom-checkbox">
  <input defaultChecked={props.list.check} type="checkbox" className="custom-control-input" id="check"/>
  <label className="custom-control-label">Paid</label>
  </div>  
  </li>
  <button type="button" onClick={()=>props.editList(props.list)} className="btn btn-outline-dark">Edit</button>
  <button type="button" onClick={()=>{props.deleteList(props.list)}} className="btn btn-outline-danger ml-3">Delete</button>

  </ul>
  </>
       
    ) 
} 