import React from 'react';
import {connect} from 'react-redux';
const UpdateList=(props)=>{
  var uobj;
         return(
           <>
           <form className="was-validated">
           <div className="form-group">
         <label htmlFor="extype">Expense Type</label>
         
         <input type="text" onChange={props.updateObj} value={props.uextype} className="form-control is-valid" id="extype" placeholder="Enter Expense Type" required/>  
     </div>
           <div className="form-group">
       <label htmlFor="desc">Description</label>
       <textarea value={props.udesc}  onChange={props.updateObj} className="form-control is-invalid" id="desc" placeholder="Enter Description" required></textarea>
     </div>
    <div className="form-group">
           <label htmlFor="amount">Amount</label>
         <input type="text" onChange={props.updateObj} value={props.uamount} className="form-control is-valid" id="amount" placeholder="Enter Amount Here" required/>
         </div>
     </form>
     <div className="custom-control custom-checkbox">
     <input onChange={props.updateObj} checked={props.ucheck} type="checkbox" className="custom-control-input" id="check"/>
     <label className="custom-control-label" htmlFor="check">Paid</label>
     </div>
     <br/>
     <button type="button" onClick={()=>{
       uobj={extype:props.uextype,desc:props.udesc,amount:props.uamount,check:props.ucheck}
       props.showUpdated(uobj);

     }} className="btn btn-outline-primary ml-6">Update</button>
           </> 
       )
         }
         const mapStateToProps = (state)=>{
            var list1=state.update.list;
            var oper=state.update.operation;
            return {
              list:list1,
              operation:oper
            }; 
          }
          const fn = connect(mapStateToProps);        
export default fn(UpdateList);
       


