import React from 'react';
import {ShowList} from './ShowList';
import {connect} from 'react-redux';
const ShowLists=(props)=>{
    return(
        <>
            {
                props.database.map((singleList)=><ShowList deleteList={props.deleteList} editList={props.editList} list={singleList}/>)  
            }
              <button type="button" onClick={props.save} className="btn btn-outline-success">Save</button>
              <button type="button" onClick={props.load} className="btn btn-outline-success ml-3">Load</button>
        </>
    )
}
const mapStateToProps = (state)=>{
    var dab=state.addition.db;
    return {
        database:dab 
    };
}
const fn = connect(mapStateToProps);
export default fn(ShowLists);