import { actionNames } from "../actiontype"
import { opr } from '../../utils/config'
import { listClass } from "../../classes/listClass";
export const addListReducer = (initState={db:[]},action)=>{
    var key;
    var newObj = new listClass();
    if(action.type===actionNames.ADD){
        
        for(let keys in newObj){
            if(keys!=='cname' && keys!=='id' && keys!=='markForDelete'){
            newObj[keys]=action.payload.oneList[keys];
            }
            }        
            for(key in newObj){
           if(key==="check"){
            if(newObj[key]===true){
                newObj.cname='list-group-item-success';}
                else if(newObj[key]===false){
                    newObj.cname='list-group-item-danger';}}
                }
                newObj.id=action.payload.id;
        action.payload.datab.push(newObj);
        opr.operationName=actionNames.ADD;
        return {...initState, db:action.payload.datab};
    }
    else if(action.type==="updateArray"){
        return{...initState,db:action.payload.datab};
    }
    else if(action.type==="local"){
        return {...initState,db:action.payload.datab};
    }
    return initState; 
}  